@use './vars' as va;

$breakpoint: (
  sp: 'screen and (max-width: 767px)',
  tb: 'screen and (min-width: 768px)',
  pc: 'screen and (min-width: 1025px)'
);

@mixin mq($bp) {
  @media #{map-get($breakpoint, $bp)} {
   @content;
  }
}

@mixin nav-item--Japanese($fontFamily:va.$serif,$fontColor:va.$text-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(18/va.$sp-design-width*100vw),18px);
  line-height: calc(24/18);
  text-align: center;
  font-weight: 500;
  writing-mode: vertical-lr;
}

@mixin nav-item--English($fontFamily:va.$english,$fontColor:va.$text-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(10/va.$sp-design-width*100vw),10px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  //text-align: center;
  font-weight: 500;
  opacity: .7;
  text-transform: uppercase;
  margin-top: max(calc(26/va.$pc-design-width*100vw),26px);
}

//sidebarのmixinのやつを変える
@mixin sidebar_item_Japanese--sp($fontFamily:va.$serif,$fontColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(18/va.$sp-design-width*100vw),18px);
  //line-height: calc(26/18);
  letter-spacing: calc(400/1000*1em);
  text-align: center;
  font-weight: 500;
  //writing-mode: vertical-lr;
}

@mixin sidebar-item--English($fontFamily:va.$english,$fontColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(10/va.$pc-design-width*100vw),10px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  //text-align: center;
  font-weight: 500;
  //opacity: .7;
  text-transform: uppercase;
  margin-top: max(calc(26/va.$pc-design-width*100vw),26px);
}


@mixin button_sub-color--sp($fontFamily:va.$serif,$fontColor:va.$text-color,$backgroudColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  background-color: $backgroudColor;
  width: 100%;
  font-size: max(calc(16/va.$sp-design-width*100vw),16px);
  letter-spacing: calc(100/1000*1em);
  font-weight: 500;
  //marginをつけたければinline-blockに
  display: block;
  text-align: center;
  line-height: calc(23/16);
  padding: max(calc(40/va.$sp-design-width*100vw),40px) 0;
}

@mixin button_sub-color--pc(){
  width:calc(380/va.$pc-design-width*100vw);
  margin: 0 auto;
  padding: calc(25/va.$pc-design-width*100vw) 0;
  font-size: max(calc(16/va.$pc-design-width*100vw),16px);
}

@mixin button_main-color--sp($fontFamily:va.$serif,$fontColor:#fff,$backgroudColor:va.$main-color){
  font-family: $fontFamily;
  color: $fontColor;
  background-color: $backgroudColor;
  width: 100%;
  font-size: max(calc(16/va.$sp-design-width*100vw),16px);
  letter-spacing: calc(100/1000*1em);
  font-weight: 500;
  display: block;
  text-align: center;
  line-height: calc(23/16);
  padding: max(calc(40/va.$sp-design-width*100vw),40px) 0;
}

@mixin button_main-color--pc(){
  width:calc(380/va.$pc-design-width*100vw);
  margin: 0 auto;
  padding: calc(25/va.$pc-design-width*100vw) 0;
  font-size: max(calc(16/va.$pc-design-width*100vw),16px);
}


//footerのナビゲーションメニュー
//必要に応じて初期値は変更
//特にフォントの変数は必ず確認する事
@mixin footer_nav_item_text--sp($fontFamily:va.$serif,$fontColor:#fff){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(18/va.$sp-design-width*100vw),18px);
  line-height: calc(23/16);
  letter-spacing: calc(100/1000*1em);
  font-weight: 500;
  margin-bottom: max(calc(20/va.$sp-design-width*100vw),20px);
  //aタグをinline-blockにしないと、レスポンシブ時マージンが効かず、文字間が詰まってしまう。
  display: inline-block;
}


